@charset "utf-8";

// RESET CSS (nykim@nykim.net)
// Last Modified : 2018-07-09

html,
body,
div,
span,
h1,
h2,
h3,
h4,
h5,
h6,
p,
em,
img,
small article,
aside,
footer,
header,
nav,
address,
section,
main,
time,
ol,
ul,
li,
hr,
strong,
figure,
tr,
th,
td,
dl,
dt,
dd,
fieldset {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  -webkit-text-size-adjust: none;
  font-style: normal;
}

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

[tabindex="-1"]:focus {
  outline: 0 !important;
}

ul,
ol,
li,
dl {
  list-style: none;
}

a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
  @include font-main;
}

a:active {
  background-color: rgba(247, 128, 81, 0.733);
}

hr {
  display: none;
}

button {
  display: block;
  margin: 0;
  padding: 0;
  @include font-main;
  border: 0;
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  background: none;
  cursor: pointer;
}

i {
  font-style: normal;
}

a,
area,
button,
[role="button"],
input:not([type="range"]),
label,
select,
summary,
textarea {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

table {
  border-collapse: collapse;
}
caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #868e96;
  text-align: left;
  caption-side: bottom;
}
th {
  text-align: inherit;
}

input {
  @include ellipsis;
  @include font-main;
  width: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  vertical-align: top;
  border-radius: 0;
  -webkit-border-radius: 0px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-border-radius: 0;
  background-color: transparent;
  border: 1px solid transparent;
}

//ios용
input[type="text"],
input[type="email"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

html {
  position: relative;
  width: 100%;
  min-width: 100%;
  min-height: 100%;
  height: 100%;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

body {
  overflow-y: scroll;
  @include font-main;
  color: #212529;
  text-align: left;
  font-weight: 400;
  line-height: 1.5;
  font-size: 0.5rem;
  background-color: #fff;
}
